/* Theme layout */
body {
  color: #596771;
  background-color: #002b4d;
}
body.no-scroll {
  overflow-y: hidden;
}

p {
  font-family: "Lora", serif;
}

h1,
h2,
h3,
h4,
h5 {
  color: #000;
  margin-bottom: 2rem;
}

img {
  max-width: 100%;
  height: auto;
}

.container-fluid--desktop {
  max-width: 1450px;
}

header {
  box-shadow: 0 0 120px 0 rgba(0, 0, 0, 0.1);
  position: relative;
  z-index: 10;
}
header .progress-container {
  width: 100%;
  height: 0.25rem;
  background: #bbd8f1;
}
header .progress-bar {
  height: 0.25rem;
  background: #06c;
  width: 0%;
}
header .doc-header-room {
  height: 4rem;
}
header.sticky #doc-header {
  position: fixed;
  top: 0;
  width: 100%;
  max-width: 1450px;
  z-index: 10;
}
header.sticky .doc-header-room {
  display: block !important;
}

.h1--header {
  font-size: 1.333rem;
  line-height: 1.2;
  letter-spacing: unset;
}
.h1--header span {
  font-size: 0.7rem;
  font-weight: 400;
}

.svg--header {
  width: 50px;
  height: 40px;
}

.h2--header {
  font-size: 14px;
  font-weight: 300;
  letter-spacing: unset;
}

.docs-italia__document-title {
  font-size: 0.9rem;
  font-weight: 400;
  margin: 0;
}

.docs-italia__document-title-icon {
  font-size: 2rem;
  float: left;
  margin-right: 0.625rem;
}

.docs-italia #cerca {
  border: 1px #ccc solid;
  font-size: 0.8rem;
  font-weight: 100;
}
.docs-italia #cerca::placeholder {
  color: #ccc;
}

.docs-italia__header-col {
  flex: 0 0 auto;
}
.docs-italia__header-col .btn {
  padding: 0;
}
.docs-italia__header-col .btn .it-icon {
  font-size: 1.3rem;
}

.info--header {
  /* background */
}

#desktop-menu {
  transform: translate(0, 0);
  /* For browsers don't support translate3d. */
  transform: translate3d(0, 0, 0);
  will-change: position, transform;
}

.col--nav {
  width: 100%;
}
@media (min-width: 768px) {
  .col--nav {
    width: 420px;
    float: left;
    border-right: 1px #eeeff0 solid;
    padding-left: 4.5rem;
  }
}

.docs-italia-menu {
  will-change: min-height;
  display: none;
}
@media (min-width: 768px) {
  .docs-italia-menu {
    display: block;
    padding-top: 1.5rem;
    bottom: 2rem;
  }
}

@media (max-width: 767.98px) {
  .docs-italia-offcanvas-menu {
    transform: translateX(100%);
    z-index: 10;
    background-color: #fff;
    transition: transform 0.3s;
    transition-timing-function: ease-in-out;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    overflow-y: auto;
  }
  .docs-italia-offcanvas-menu.active {
    transform: translateX(0);
  }
}
@media (min-width: 768px) {
  .docs-italia-offcanvas-menu {
    display: none;
  }
}

.navbar-toggler--wrap {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 9;
  background-color: #fff;
  box-shadow: -1px -2px 62px -13px rgba(0, 0, 0, 0.25);
}
@media (min-width: 768px) {
  .navbar-toggler--wrap {
    display: none;
  }
}

.navbar-toggler {
  color: #06c;
  padding: 1rem 1rem 1rem 2rem;
}

.navbar-toggler__chapter {
  color: #000;
  font-size: 0.8rem;
}

.sidebar-tabs {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: #fff;
  transform: translateX(100%);
  transition: transform 0.3s;
  transition-timing-function: ease-in-out;
  z-index: 11;
  box-shadow: -1px -2px 62px -13px rgba(0, 0, 0, 0.25);
}
.sidebar-tabs.active {
  transform: translateX(0);
}
@media (min-width: 768px) {
  .sidebar-tabs {
    display: none;
  }
}

.sidebar-btn {
  text-transform: uppercase;
  color: #06c;
  font-weight: 800;
  cursor: pointer;
  border: 0;
  border-bottom: 3px #0073e6 solid;
  background: none;
}
@media (min-width: 768px) {
  .sidebar-btn {
    border-bottom: 2px #bbd8f1 solid;
  }
}
.sidebar-btn.sidebar-btn--mobile {
  display: inline-block;
  font-size: 0.7rem;
  text-align: center;
  border: 0;
}
.sidebar-btn.sidebar-btn--mobile.active.show {
  border-bottom: 3px #0073e6 solid;
  text-decoration: none;
}
@media (min-width: 768px) {
  .sidebar-btn.sidebar-btn--mobile.active.show {
    border-bottom: 2px #bbd8f1 solid;
  }
}
.sidebar-btn.focus--mouse {
  border-color: #0073e6 !important;
}
@media (min-width: 768px) {
  .sidebar-btn.focus--mouse {
    border-color: #bbd8f1 !important;
  }
}
.sidebar-btn .it-icon-expand,
.sidebar-btn .it-icon-collapse {
  font-size: 0.6rem;
  line-height: 2.5;
}
.sidebar-btn .it-icon-expand {
  display: inline-block;
}
.sidebar-btn .it-icon-collapse {
  display: none;
}
.sidebar-btn.collapsed .it-icon-expand {
  display: none;
}
.sidebar-btn.collapsed .it-icon-collapse {
  display: inline-block;
}
@media (min-width: 768px) {
  .sidebar-btn {
    display: block;
    width: 100%;
    text-align: left;
  }
}
@media (max-width: 767.98px) {
  .sidebar-btn .it-icon-expand,
  .sidebar-btn .it-icon-collapse {
    display: none;
  }
  .sidebar-btn.collapsed .it-icon-expand,
  .sidebar-btn.collapsed .it-icon-collapse {
    display: none;
  }
}

.main-container {
  overflow: hidden;
  position: relative;
  background-color: #fff;
}

.doc-content__consultation {
  position: absolute;
  right: 0;
  left: 0;
  top: 0;
  background-color: #4bd4d6;
  text-align: left;
  padding: 0.5rem 0;
  text-transform: uppercase;
  font-weight: 800;
  color: #000;
  padding-left: 2rem;
}
@media (min-width: 992px) {
  .doc-content__consultation {
    padding-left: 4rem;
  }
}
@media (min-width: 1200px) {
  .doc-content__consultation {
    transform: rotate(45deg) translate(45px, -125px);
    width: 320px;
    transform-origin: left;
    z-index: 1;
    left: auto;
    text-align: center;
    padding-left: 0;
  }
}

.col-content {
  padding: 4rem 2rem;
}
@media (min-width: 992px) {
  .col-content {
    padding: 4rem 4rem 5rem 4rem;
  }
}

/* Caption */
.caption-wrap {
  clear: both;
}
.caption-wrap .caption--table {
  margin-top: 2rem;
  margin-bottom: 2rem;
  display: block;
  font-family: "Lora", serif;
  font-style: italic;
  font-size: 0.75rem;
  padding: 0;
}
@media (min-width: 992px) {
  .caption-wrap .caption--table {
    width: calc(100% - 200px);
    float: left;
  }
}
.caption-wrap .reference--wrap {
  font-family: "Titillium Web", "Helvetica Neue", Helvetica, Arial, sans-serif;
  margin-top: 1.5rem;
  margin-bottom: 2rem;
}
@media (min-width: 768px) {
  .caption-wrap .reference--wrap {
    width: 200px;
    float: right;
    padding-left: 2rem;
  }
}

.caption-number {
  font-weight: 800;
  color: #000;
}

/* Title H1/H2 */
.chapter-header {
  margin-bottom: 1rem;
  margin-left: 0.5rem;
}

.chapter-nav {
  position: relative;
}
@media (min-width: 992px) {
  .chapter-nav {
    width: 200px;
    float: left;
  }
}

.figure-fixed-wrap {
  display: block;
  width: 100%;
  overflow-x: auto;
}

.figure-fixed {
  max-width: none;
}

/* Title nav */
.title__background {
  width: 100%;
  background-color: #e6f3fe;
  position: absolute;
  top: -1px;
  height: auto;
  left: -0.5rem;
  display: none;
  right: 0;
  z-index: 0;
}
.title__background::after {
  content: "";
  position: absolute;
  left: 100%;
  transform: translateX(-50%);
  top: 0;
  bottom: 0;
  width: 0;
  height: 0;
  border-left: 20px solid transparent;
  border-right: 20px solid transparent;
  border-top: 20px solid #e6f3fe;
}

/* Term */
.std-term {
  background-color: #bffffd;
  color: #000;
  padding: 0.2rem;
}

/* Footer btn */
.footer-buttons--next {
  clear: both;
  margin-top: 4rem;
}
.footer-buttons--next a {
  background-color: #0073e6;
  color: #fff;
  padding: 0.5rem 1rem;
  border-radius: 0.2rem;
  font-weight: 800;
  display: inline-block;
}

.footer-buttons--prev {
  margin-bottom: 2.5rem;
}

/* Reference icon */
.reference.internal {
  vertical-align: middle;
}

.reference-icon {
  display: inline-block;
}

/* General width */
#doc-content {
  width: 100%;
  position: relative;
}
@media (min-width: 768px) {
  #doc-content {
    width: calc(100% - 420px);
    float: left;
  }
}
#doc-content .useful-docs .mimetype {
  display: none;
}
#doc-content .useful-docs ul {
  list-style-type: none;
  padding-left: 0;
}
#doc-content .useful-docs li {
  border-top: 1px #ccc solid;
  padding-top: 1rem;
  padding-bottom: 1rem;
  vertical-align: middle;
}
#doc-content .useful-docs li::after {
  display: none;
}
#doc-content .useful-docs .Icon {
  font-size: 1.9rem;
  color: #0073e6;
}
#doc-content .procedure ol {
  padding-left: 1rem;
}
#doc-content .procedure ol li {
  position: relative;
  margin-bottom: 1rem;
  padding-left: 1rem;
  counter-increment: list;
  list-style-type: none;
  font-family: "Lora", serif;
}
#doc-content .procedure ol li::after {
  position: absolute;
  left: -1rem;
  top: 0;
  width: 1.1rem;
  height: 1.5rem;
  z-index: 1;
  content: counter(list);
  color: #fff;
  text-align: center;
  font-family: "Titillium Web", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-weight: 600;
}
#doc-content ul.simple {
  list-style-type: none;
}
#doc-content ul.simple li {
  position: relative;
  margin-bottom: 0.5rem;
  font-size: 0.9rem;
}
#doc-content ul.simple li::after {
  content: '';
  width: 0;
  height: 0;
  border-top: 5px solid transparent;
  border-bottom: 5px solid transparent;
  border-left: 5px solid #596771;
  position: absolute;
  left: -1rem;
  top: 0.4rem;
}
#doc-content .section {
  clear: both;
}
#doc-content .section::after {
  content: "";
  display: table;
  clear: both;
}
@media (min-width: 992px) {
  #doc-content .section > * {
    width: calc(100% - 200px);
    float: left;
    clear: left;
  }
}
@media (min-width: 992px) {
  #doc-content .section .section,
  #doc-content .section .chapter-header,
  #doc-content .section .caption-wrap,
  #doc-content .section .table-responsive,
  #doc-content .section .figure,
  #doc-content .section .full-width {
    width: 100%;
  }
}
#doc-content .procedure.topic {
  margin-bottom: 3rem;
}
#doc-content .procedure.topic em {
  font-weight: 800;
}
#doc-content .procedure__img {
  padding-left: 1.5rem;
  margin-bottom: 2rem;
  margin-left: 0.5rem;
  margin-top: 2rem;
  border-left: 1px #799ebc solid;
}
#doc-content .procedure__img img {
  box-shadow: 0 0 61px 0 rgba(0, 0, 0, 0.15);
}
#doc-content .procedure-internal-title {
  text-transform: uppercase;
  color: #000;
  font-family: "Titillium Web", "Helvetica Neue", Helvetica, Arial, sans-serif;
  font-size: 0.8rem;
  font-weight: 800;
}
#doc-content .Icon--ol {
  color: #004e95;
  position: absolute;
  left: -1.5rem;
  font-size: 2rem;
}
@media (min-width: 992px) {
  #doc-content .title-wrap {
    width: calc(100% - 200px);
    float: left;
    clear: left;
    position: relative;
    /* line-height is defined in px instead rem for background element crossbrowser */
  }
  #doc-content .title-wrap h2 {
