  font-family: "Titillium Web", "Helvetica Neue", Helvetica, Arial, sans-serif;
}
#glossary-page .term-content p {
  font-family: "Titillium Web", "Helvetica Neue", Helvetica, Arial, sans-serif;
}
#glossary-page .term-content:last-of-type.show {
  border-bottom: 2px #eaebed solid;
